export default {
    //播放列表
    playlist_id: null,
    playlist : [],
    //播放状态
    status : 1,
    //循环状态
    loop: 1,
    //加载中
    loading : true,
    //当前播放对象
    current : null,
    //当前播放对象在播放列表的位置
    current_index: null,
    //歌词
    lyric: null,
    //上次播放的歌曲id
    last_id: null
}


